@keyframes load {
  0% {
    opacity: 0;
    transform: translateY(-97px);
  }
  15% {
    opacity: 0;
    transform: translateY(-97px);
  }
  30% {
    opacity: 1;
    transform: translateY(0);
  }
  70% {
    opacity: 1;
    transform: translateY(0);
  }
  85% {
    opacity: 0;
    transform: translateY(97px);
  }
  100% {
    opacity: 0;
    transform: translateY(97px);
  }
}

